@import "../styles/variables";

.launch-details {
    display: flex;
    min-height: 100vh;
    flex-direction: column;

    &-navbar {
        background: $dark-grey;
        padding: 0 44px; }

    &-body-wrapper {
        flex: 1;
        display: flex;
        justify-content: center;
        .launch-details-body {
            padding: 44px; } }

    &-footer {
        background: $dark-grey;
        padding: 0 44px; } }

.layout {
    display: flex;
    align-items: baseline;
    .layout__left, .layout__right {
        flex: 1; } }
